import React from "react";
import { Icon, InlineIcon } from "@iconify/react";
import javascriptIcon from "@iconify/icons-logos/javascript";
import codeFile from "@iconify/icons-icons8/code-file";
import fileCodeO from "@iconify/icons-fa/file-code-o";
import codeIcon from "@iconify/icons-oi/code";
import codeEditor from "@iconify/icons-openmoji/code-editor";
import { NavLink } from "react-router-dom";

const Header = (props) => {
  return (
    <header className="masthead mb-auto">
      <nav class="navbar navbar-expand-sm navbar-dark bg-transparent">
        <a href="/" class="navbar-brand d-flex align-items-center">
          <Icon className="mr-2 logo" icon={codeEditor} />
          <b>Jose Mendez</b>
        </a>
        <button
          class="navbar-toggler collapsed"
          type="button"
          data-toggle="collapse"
          data-target="#navbarsExample04"
          aria-controls="navbarsExample04"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>

        <div class="navbar-collapse collapse" id="navbarsExample04">
          <ul class="navbar-nav mr-auto">
            <li class="nav-item">
              <NavLink className="nav-item nav-link" to="/blog">
                Blog
              </NavLink>
            </li>
            <li class="nav-item">
              <NavLink className="nav-item nav-link" to="/musica">
                Música
              </NavLink>
            </li>
          </ul>
          {/* <form class="form-inline my-2 my-md-0">
            <input
              class="form-control"
              type="text"
              placeholder="Buscar"
            ></input>
          </form> */}
        </div>
      </nav>
    </header>
  );
};

export default Header;
