import React, { useRef, useEffect } from "react";
import Header from "./header";
import Footer from "./footer";
import Hightlighter from "./highlighter";

const Articulo1 = () => {
  return (
    <>
      <Header />
      <div class="blog-post text-left">
        <strong class="d-inline-block mb-2 text-lime">React 16.8</strong>
        <h2 class="blog-post-title">¿Cómo usar los hooks?</h2>
        <p class="blog-post-meta">
          Julio 16, 2020 por <a href="#">Jose</a>
        </p>

        <p>
          Entender como funcionan los hooks puede resultar un poco confuso
          debido a su sintaxis específica, al contexto en el que se ejecutan y a
          las reglas que debemos seguir para aplicarlos. Sin embargo, aclarando
          unos cuantos conceptos podemos empezar a implementarlos sin mayor
          dificultad.
        </p>

        <h4>Introducción</h4>
        <p>
          Los hooks son una nueva característica en React 16.8 y vienen a romper
          con el concepto de <u>Stateless Components</u>, el cual se utilizaba
          para referirse a los Componentes Funcionales. Ahora gracias a los
          hooks los Componentes Funcionales dejaron de ser "stateless" , es
          decir, que pueden hacer uso del state o estado.
        </p>

        <p>
          Además del uso del state, los hooks también le proporcionan a los
          Componentes Funcionales la capacidad de hacer uso de los{" "}
          <u>Lifecycle Hooks</u> mediante una nueva sintaxis definida que
          veremos más adelante.
        </p>

        <h4>Tipos de hooks</h4>
        <p>Los hooks dependiendo su tipo se dividen de la siguiente manera:</p>

        <ul>
          <b>Hooks básicos:</b>
          <li>useState</li>
          <li>useEffect</li>
          <li>useContext</li>
        </ul>
        <ul>
          <b>Hooks adicionales:</b>
          <li>useReducer</li>
          <li>useCallback</li>
          <li>useMemo</li>
          <li>useRef</li>
          <li>useImperative</li>
          <li>useLayoutEffect</li>
          <li>useDebugValue</li>
        </ul>

        <h4>Reglas de los hooks</h4>
        <p>
          Como veremos más adelante se puede llamar multiples veces a un hook
          dentro de un componente. React se basa en el orden que los hooks de
          estado son llamados por lo que una de las reglas es que no deben estar
          dentro de ciclos, condicionales o funciones.
        </p>

        <p>
          Una segunda regla es que los hooks deben ser llamados solo desde
          funciones de react, es decir, no deben ser llamados desde funciones
          regulares de javascript.
        </p>

        <h4>useState (hook de estado)</h4>
        <p>
          Nos permite hacer uso del estado dentro de una función. El hook
          useState al igual que los demás, es una función que en este caso
          recibe como argumento el valor inicial de state y devuelve un arreglo
          con dos elementos:
        </p>
        <ul>
          <li>[Posicion 0]: el valor del state.</li>
          <li>[Posición 1]: una función para modificar dicho valor.</li>
        </ul>

        <p>
          Existe una convención general sobre la manera de declarar el state
          usando useState, que es haciendo la desestructuración del arreglo al
          mismo tiempo que se manda a llamar al hook y nombrando al primer
          elemento del array con un nombre arbitraro y al segundo elemento del
          array con el prefijo "set" seguido del mismo nombre, como en el
          siguiente ejemplo:
        </p>

        <Hightlighter>{`import React, { useState } from 'react';

function Example() {
  // Declaración de una variable de estado que llamaremos "count"
  const [count, setCount] = useState(0);

  return (
    <div>
      <p>You clicked {count} times</p>
      <button onClick={() => setCount(count + 1)}>
        Click me
      </button>
    </div>
  );
}`}</Hightlighter>

        <p></p>

        <h4>¿Debo empezar a usar los hooks?</h4>
        <p>
          El equipo de react en Facebook recomienda su implementación para
          nuevos componentes o bien para aquellos componentes funcionales que
          pensabamos reescribir como clase. Es verdad que la apuesta por los
          componentes funcionales es fuerte, sin embargo no existe hasta el
          momento ningun plan para dejar fuera a los componentes clase (facebook
          tiene miles en producción).
        </p>

        <p>
          Además de esto, el equipo de react es muy renunte a aplicar cambios
          drásticos sin contar con el concenso general de la comunidad de
          desarrolladores, por lo que habrá que esperar un poco más para ver que
          sucede.
        </p>
      </div>

      <Footer />
    </>
  );
};

export default Articulo1;
