import React from "react";
import Header from "./header";
import Footer from "./footer";

const Index = () => {
  return (
    <>
      <Header />
      <main role="main" class="inner cover">
        <h1 class="cover-heading">React | Jr.</h1>
        <p className="lead">
          Información útil para el desarrollo en #reactjs, enfocado en JR
          Developers. Hecho por uno de ellos.
        </p>
      </main>
      <Footer />
    </>
  );
};

export default Index;
